.contact{width:100%;height:375px;padding:20px;display:flex;align-items:center;justify-content:center;background-color:var(--soft-blue)}.contact .content{display:flex;flex-direction:column;align-items:center;gap:30px}@media(max-width: 500px){.contact .content{gap:20px}}.contact .content .alreadyText{color:var(--white);letter-spacing:4px;font-size:14px}@media(max-width: 500px){.contact .content .alreadyText{font-size:12px}}.contact .content h1{max-width:450px;color:var(--white);text-align:center;font-weight:500}@media(max-width: 500px){.contact .content h1{font-size:22px;max-width:300px}}@media(max-width: 350px){.contact .content h1{font-size:20pxÑ‚}}.contact .content .contactForm{display:flex;align-items:baseline;gap:20px}@media(max-width: 600px){.contact .content .contactForm{flex-direction:column;gap:10px}}.contact .content .contactForm .entry{display:flex;flex-direction:column;position:relative;padding:4px 4px 10px 4px;border-radius:4px;gap:10px;transition:all .3s}.contact .content .contactForm .entry.error{transition:all .4s;background:var(--soft-red)}.contact .content .contactForm .entry input{width:325px;height:50px;font-size:16px;color:var(--dark-gray);border:2px solid rgba(0,0,0,0);border-radius:4px;border:none;padding:0px 40px 0px 20px}@media(max-width: 400px){.contact .content .contactForm .entry input{width:275px;font-size:14px}}@media(max-width: 350px){.contact .content .contactForm .entry input{width:250px;font-size:14px}}.contact .content .contactForm .entry input::placeholder{color:#d0d0d0}.contact .content .contactForm .entry .errorIcon{position:absolute;right:10px;top:18.5px;transition:all .4s}.contact .content .contactForm .entry .errorText{color:var(--white);font-size:12px;font-style:italic;letter-spacing:1px;position:relative;left:10px;transition:all .4s}.contact .content .contactForm button{padding:12px 15px;height:50px;font-size:14px;letter-spacing:1px;font-weight:500;background-color:var(--soft-red);border:2px solid rgba(0,0,0,0);border-radius:4px;color:var(--white);box-shadow:0px 0px 10px -3px var(--dark-blue);transition:all .4s}@media(max-width: 600px){.contact .content .contactForm button{width:100%}}.contact .content .contactForm button:hover{transition:all .4s;border:2px solid var(--soft-red);background:var(--white);color:var(--soft-red);cursor:pointer}